*{
    box-sizing: border-box;
    margin: 0;
    padding: 0
}
body{
    background: #181818;
}

@font-face {
    font-family: 'Montserrat-bold';
    src: url('static/Montserrat-Bold.ttf');
}
@font-face {
    font-family: 'Montserrat-medium';
    src: url('static/Montserrat-Medium.ttf');
}

section{
    position: relative;
    background: #181818;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
}

section .animation{
    position: relative;
    width: 50px;
    height: 50px;
    background: #288ee7;
    border-radius: 50px;
    box-shadow: 0 0 20px 10px rgba(52, 152, 219, 0.8); /* Efek glow */
    
}

section .different{
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    font-family: 'Montserrat-bold';
    font-size: 50px;
}

section .high {
    font-family: 'Montserrat-bold';
}

section .low{
    font-family: 'Montserrat-medium';
    /* margin: 0px; */
}

section .different:hover{
    text-shadow: 
        0 0 30px white, /* Efek glow ketiga (lebih lebar) */
        0 0 40px white, 
        0 0 50px white;

    transition: ease .3s;
}


   /* about me  */

.tools{
    background: #181818;
    height: 100vh;
    transition: ease 1s;
}


a {
    color: white;
    text-decoration: none;
    font-family: 'Montserrat-medium';
}
a:hover{
    color: #288ee7; /* Biru tosca saat hover */
    text-shadow: 2px 2px 10px #288ee7; /* Bayangan biru tosca */
    transition: ease .3s;
}
.bg-navigation{
    background-color: #232323;
    
    
}

.margin-tp{
    margin-top: 400px;
}

.outline-about {
    height: 600px;
    border: solid 3px  #288ee7;
    
}

.bg-black-ts{
    background-color: rgba(0, 0, 0, .3);
    box-shadow: 4px 5px 81px -5px rgba(0,0,0,0.63);
-webkit-box-shadow: 4px 5px 81px -5px rgba(0,0,0,0.63);
