.width-foto{
    width: 250px;
    height: 250px;
}
.position-foto{
    transform: translateY(-80px);
}

.line-edit{
    width: 50%;;
    height: 2px;
    background-color: #288ee7;
    box-shadow: 0 0 20px 2px rgba(46, 95, 128, 0.8); /* Efek glow */
    margin-left: 32px;
}
.glow-foto{
    background-color: #288ee7;
